.modal-background-box {
    width: 1200px;
    height: 70vh;
    overflow: auto;
    background-color: white;
    position: absolute;  /* 상위 요소에 대한 절대 위치 */
    top: 40%;            /* 상위 요소의 중앙에 위치 */
